<template>
  <v-container class="pb-15">

    <v-row no-gutters>
      <v-col>
        <v-breadcrumbs class="pa-0" :items="breadcrumb"></v-breadcrumbs>
      </v-col>
    </v-row>

    <v-row>
      <v-col>

        <v-list-item two-line class="pa-0">
          <v-list-item-content class="pa-0">
            <v-list-item-title class="text-h6 text-wrap">
              Proyecto: {{ data.name }}
            </v-list-item-title>
            <v-list-item-subtitle class="text-h7 text-wrap mb-4 mb-sm-0">
              Categoría: {{ data.category }}
            </v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>

        <h4>Descripción del proyecto:</h4>
        <v-list-item class="pa-0" v-if="data.description.length > 0">
          <ul>
            <li v-for="(item, index) in data.description" :key="index">
              {{item}}
            </li>
          </ul>
        </v-list-item>

        <v-list-item class="pa-0">
          <v-chip :color="data.pending ? 'red' : 'green'">
            <b class="white--text">{{ data.pending ? 'Proyecto sin finalizar' : 'Proyecto Finalizado' }}</b>
          </v-chip>
        </v-list-item>

      </v-col>
    </v-row>

    <v-row v-if="$vuetify.breakpoint.smAndUp">
      <v-col>
        <v-carousel v-model="model" @change="onSlideChange" :continuous="false" class="carousel" height="100%">
          <v-carousel-item v-for="(asset, i) in data.assets" :key="i">
            <template v-if="isImage(asset)">
              <v-img style="height: 90%" contain :src="`${publicPath}${data.url}/${asset}`" @click="previewImage(`${publicPath}${data.url}/${asset}`)"></v-img>
            </template>
            <template v-else-if="isVideo(asset)">
              <video style="height: 90%" ref="video" :src="`${publicPath}${data.url}/${asset}`" controls></video>
            </template>
          </v-carousel-item>
        </v-carousel>
      </v-col>
    </v-row>

    <v-row v-else>
      <v-col cols="12" v-for="(asset, i) in data.assets" :key="i">
        <template v-if="isImage(asset)">
          <v-img :src="`${publicPath}${data.url}/${asset}`" @click="previewImage(`${publicPath}${data.url}/${asset}`)"></v-img>
        </template>
        <template v-else-if="isVideo(asset)">
          <video :src="`${publicPath}${data.url}/${asset}`" controls></video>
        </template>
      </v-col>
    </v-row>

    <v-dialog content-class="assets-preview" v-model="preview.show" scrollable>
      <v-card light color="transparent">
        <v-toolbar flat color="transparent">
          <v-spacer></v-spacer>
          <v-btn text @click="preview.show = false" icon color="white">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>

        <v-card-text class="pa-0">
          <div class="fill-height">
            <v-img contain class="fill-height" :src="preview.url"></v-img>
          </div>
        </v-card-text>
      </v-card>
    </v-dialog>

  </v-container>
</template>

<script>
import projects from '../assets/projects'

export default {
  data: () => ({
    publicPath: process.env.BASE_URL,
    data: {},
    model: 0,
    preview: {
      show: false,
      url: ''
    }
  }),
  beforeMount() {
    var data = projects.find(item => item.id === this.$route.params.id);
    if (typeof data === 'undefined') return this.$router.push('/projects');
    this.data = data;
  },
  methods: {
    getAssetType(url) {
      var _url = url.split('.');
      switch (_url[1]) {
        case 'png': return 'image';
        case 'mp4': return 'video';
      }
    },
    isImage(url) {
      return this.getAssetType(url) === 'image';
    },
    isVideo(url) {
      return this.getAssetType(url) === 'video';
    },
    onSlideChange() {
      if (this.$refs.video) {
        this.$refs.video.forEach(video => {
          video.pause();
          video.currentTime = 0;
        });
      }
    },
    previewImage(asset) {
      this.preview.url = asset;
      this.preview.show = true;
    }
  },
  computed: {
    breadcrumb() {
      return [
        {
          text: 'Proyectos',
          // href: '/projects',
          exact: true,
          to: '/projects'
        },
        {
          text: this.data.name,
          disabled: true
        }
      ];
    }
  }
}
</script>

<style lang="scss">
@import "~vuetify/src/styles/settings/_variables";
@import "~vuetify/src/styles/settings/_colors";

video {
  width: 100%;
  object-fit: contain;
}

.v-item--active {
  color: map-get($light-blue, "darken-1") !important;
}

.carousel {
  height: 100%;
}

@media #{map-get($display-breakpoints, "md-and-up")} {
  .carousel {
    height: 500px !important;
  }
}

.v-dialog.assets-preview {
  border-radius: 0;
  box-shadow: none;
}
</style>